@import url(https://fonts.googleapis.com/css?family=Poppins:400,700,900&display=swap);
#drumMachine {
	margin: 0 auto;
	width: 40%;
	padding: 1em;
	background: #ef7508;
	border-radius: 3px;
	box-shadow: -10px 10px 0px #ca6209;
}

.title {
	font-style: italic;
	color: #333;
	text-align: right;
}

.modeTag {
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
	-webkit-transform-origin: center;
	        transform-origin: center;
	font-size: 8px;
}

.modeSelector {
	display: flex;
	justify-content: flex-end;
	
	margin-bottom: 2em;
	padding: 0.3em 0.3em 1.5em 0.3em;
	border-bottom: 3px dashed #333;
	color: #333;
}

.modeSelector li {
	display: flex;
	
	align-items: center;
	list-style-type: none;
}

.modeSelector li>span {
	font-size: 7px;
	margin: 0 5px;
}

.modeSwitcher {
	padding: 0.2em 0.7em;
	background-repeat: no-repeat;
	background-size: 100% 100%;
}

.drumsSwitcher {
	background-image: url(/drum-machine-react-build/static/media/switcherUp.c27ce2ba.svg);
}

.synthSwitcher {
	background-image: url(/drum-machine-react-build/static/media/switcherDown.555dfbea.svg);
}

.modeSelectorLED ul {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.board {
	display: grid;
	
	height: 80%;
}

.board-drums {
	grid-template-columns: 33.3% 33.3% 33.3%;
}

.board-synth {
	grid-template-columns: 25% 25% 25% 25%;
}

.key {
	display: flex;
	align-items: flex-end;
	
	margin: 0.3em 0.3em;
	padding-top: 50%;
	padding-bottom: 0.3em;
	padding-left: 0.3em;
	background: #bdbbbb;
	color: #dbdbdb;
	text-shadow: 0px 1px 0px #a8a8a8;
	border-bottom: 2px solid #8a8888;
	border-left: 2px solid #8a8888;
	border-radius: 3px;
	cursor: pointer;
}

.key-modeSwitcher {
	display: flex;
	flex-direction: column;
	align-content: space-between;
	
	padding: 0.3em;
	margin-right: 0.3em;
	background: #bdbbbb;
	color: #dbdbdb;
	text-shadow: 0px 1px 0px #a8a8a8;
	border-bottom: 2px solid #8a8888;
	border-left: 2px solid #8a8888;
	border-radius: 3px;
	cursor: pointer;
}

.key-active {
	background: #8a8888;
	border-top: 2px solid #ef7508;
	border-right: 2px solid #ef7508;
	border-bottom: none;
	border-left: none;
}
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

*::-moz-selection {
	background: transparent;
}

*::selection {
	background: transparent;
}

body {
	display: flex;
	align-items: center;
	
	height: 100vh;
	font-family: 'Poppins', sans-serif;
	font-weight: normal;
	font-size: 100%;
	margin: 0;
	padding: 0;
	background: #d9d9d9;
}
@media (max-width: 880px) {
	#drumMachine {
		width: 60%;
	}
}

@media (max-width: 620px) {
	#drumMachine {
		width: 90%;
	}
}
